var swiper = new Swiper(".reviews__slider", {
  pagination: {
    el: '.custom-progressbar',
    type: 'progressbar',
    renderProgressbar: function (progressbarFillClass) {
      return '<div class="' + progressbarFillClass + ' custom-progress"></div>';
    },
  },
  navigation: {
    nextEl: ".reviews__nav-btn--next",
    prevEl: ".reviews__nav-btn--prev",
  },
  slidesPerView: 1,
  spaceBetween: 15,
  effect: "slide",
  rewind: true,
  speed: 500,
  breakpoints: {
    550: {
      slidesPerView: 2,
    },
    900: {
      slidesPerView: 3,
      spaceBetween: 30,
    },
    1024: {
      slidesPerView: 3.2,
      spaceBetween: 30,
    },
    1100: {
      slidesPerView: 3.5,
      spaceBetween: 30,
    },
    1200: {
      slidesPerView: 4,
      spaceBetween: 30,
    },
    1400: {
      slidesPerView: 4.5,
      spaceBetween: 30,
    },
  },
  autoplay: {
    delay: 3500,
    disableOnInteraction: false,
  },
});

var aboutSwiper = new Swiper(".about__slider", {
  spaceBetween: 30,
  speed: 1000,
  navigation: {
    nextEl: ".about__nav-btn--next",
    prevEl: ".about__nav-btn--prev",
  },
  breakpoints: {
    768: {
      autoplay: {
        delay: 3500,
        disableOnInteraction: false,
      },
    }
  },
});

var allSwiper = new Swiper(".all__slider", {
  slidesPerGroup: 1,
  slidesPerView: 1,
  freeMode: true,
  effect: "slide",
  rewind: true,
  speed: 500,
  grid: {
    rows: 2,
    fill: 'row',
  },
  spaceBetween: 10,
  observer: true,
  observeParents: true,
  navigation: {
    nextEl: document.querySelector(".all--next"),
    prevEl: document.querySelector(".all--prev"),
  },
  on: {
    init: function () { },
    orientationchange: function () { },
    beforeResize: function () {
      let vw = window.innerWidth;
      if (vw > 1000) {
        allSwiper.params.slidesPerView = 2.4
        allSwiper.params.slidesPerColumn = 1
        allSwiper.params.slidesPerGroup = 1;
      } else {
        allSwiper.params.slidesPerView = 1.1
        allSwiper.params.slidesPerColumn = 1;
        allSwiper.params.slidesPerGroup = 1;
      }
      allSwiper.init();
    },
  },
});

